<template>
  <el-table id="tab" height="300" :data="tableData">
    <el-table-column prop="name" label="Live Node Name"/>
    <el-table-column prop="addr" label="Live Node Address"/>
    <el-table-column label="Operations">
      <template #default="scope">
        <el-button v-if="scope.$index === 0" size="small" @click="dialogVisible = true">Login</el-button>

        <el-button v-if="scope.$index === 0" size="small" @click="adminGetFlag">GetFlag</el-button>
      </template>
    </el-table-column>

  </el-table>
  <el-dialog class="custom-dialog" v-model="dialogVisible" title="Login To Admin" width="30%" draggable>
    <el-input class="name" v-model="AccessKey" placeholder="Please input AccessKey" clearable size="large"/>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="dialogVisible = false">Cancel</el-button>
        <el-button type="primary" @click="adminLogin">
          Login
        </el-button>
      </span>
    </template>
  </el-dialog>
</template>

<script>
import {getCurrentInstance} from "vue";
import {ElMessage, ElMessageBox} from "element-plus";


export default {
  name: "LiveNodesTable",
  data(){
    return{
      tableData : [
          {name:"admin_manage",addr:"127.0.0.1"},
      ],
      AccessKey: "",
      parsedData:null,
      dialogVisible:false,
      path:"",
    }
  },
  methods: {
    getLiveNodes(){
      const self = this;
      this.axios.get(`/api/server/nodes`)
          .then(function (data) {
            const liveNode = data.data.urls
            for (const key in liveNode) {
              let tmpDate = {
                name:key,
                addr:liveNode[key],
              }
              self.tableData.push(tmpDate)
            }
          })
          .catch(function (error) {
            console.error(error);
            ElMessage({
              message: error,
              type: 'error',
            });
          });
      // console.log(this.tableData)
    },
    adminGetFlag(){
      this.axios.get(`/api/server/getflag`)
          .then(function (data) {
            if (data.data.success) {
              ElMessageBox.alert(
                  data.data.message,
                  'Get Flag',
              )
            }else {
              throw new Error(data.data.message)
            }
          })
          .catch(function (error) {
            ElMessage({
              message: error,
              type: 'error',
            });
          });
    },
    adminLogin(){
      this.axios.get(`/api/server/admin/login?accesskey=${this.AccessKey}`)
          .then(function (data) {
            if (data.data.success) {
              ElMessageBox.alert(
                  data.data.message,
                  'Admin Manager Login',
              )
            }else {
              throw new Error(data.data.message)
            }
          })
          .catch(function (error) {
            ElMessage({
              message: error,
              type: 'error',
            });
          });
      this.dialogVisible = false;
    },
  },
  setup(){
    const getAxios = ()=>{
      return getCurrentInstance().appContext.config.globalProperties.$http
    }
    return {
      getAxios
    }
  },
  mounted() {
    this.axios = this.getAxios();
    this.getLiveNodes()
  },
}
</script>

<style>
#tab {
  width: 60%;
  margin: 0 auto;
  /*background-color: #047e7e;*/
  border-radius: 8px;
}
.el-table .el-table__header-wrapper {
  background-color: #047e7e;
}

.custom-dialog{
  border-radius: 8px;
}
</style>
